import { Route, Routes } from 'react-router-dom';
import Home from '../../components/ReactJS/PracticaEnrutamiento/Home';
import ParamsId from '../../components/ReactJS/PracticaEnrutamiento/ParamsId';
import StarwarsAPI from '../../components/ReactJS/StarwarsAPI';
import ReactRoutingIndex from '../../Introduction/2-ReactJS/5-ReactRoutingIndex';

const ReactRoutingRoutes = () =>{

    const commonData = {
        title: 'Prctica de enrutamiento',
        description: 'Crear una aplicacin React simple que pueda manejar las distintas rutas utilizando el hook useParams()',
        objectives: ['Crear la ruta "localhost:3000/home" Esto debera mostrar la pgina de inicio, con un encabezado genrico de "Bienvenida".', 'Crear la ruta "localhost:3000/4" Esto debera mostrar el nmero "4", o cualquier otro nmero que se haya enviado.', 'Crear la ruta "localhost:3000/hello" Esto debera mostrar la palabra "hello" o cualquier otra palabra que se haya enviado.', 'Crear la ruta "localhost:3000/hola/azul/rojo" Esto debera mostrar la palabra "hola" en texto azul con un fondo rojo. Debera funcionar con cualquier otra palabra y colores vlidos.']
    };

    return(
        <div>
            <Routes>
                <Route path="/" element={<ReactRoutingIndex/>}/>
                <Route path="/practica-de-enrutamiento" element={<Home commonData={commonData}/>}/>
                <Route path="/practica-de-enrutamiento/:id" element={<ParamsId commonData={commonData}/>}/>
                <Route path="/practica-de-enrutamiento/:id/:color/:bgcolor" element={<ParamsId commonData={commonData}/>}/>
                <Route path="/luke-apiwalker" element={<StarwarsAPI />}/>
            </Routes>
        </div>
    )
}

export default ReactRoutingRoutes;